import React from 'react'
import BurgerStyleMenu from '../scss/BurgerMenu.module.scss'
import auto from '../../../img/Header/auto.svg'
import cargoTruck from '../../../img/Header/cargoTruck.svg'

export const BurgerMenu = ({ActiveMenu}) => {

    return (
        <div className={BurgerStyleMenu.BurgerMenu}>
            <div className={BurgerStyleMenu.column}>
                <ul>
                    <li>
                        <a href="#SectionWhyMe" onClick={ActiveMenu}>{'Почему мы'}</a>
                    </li>
                    <li>
                        <a href="#Evacuate" onClick={ActiveMenu}>{'Услуги'}</a>
                    </li>
                    <li>
                        <a href="" onClick={ActiveMenu}>{'Цены'}</a>
                    </li>
                    <li>
                        <a href="#Work" onClick={ActiveMenu}>{'Работы'}</a>
                    </li>
                    <li>
                        <a href="" onClick={ActiveMenu}>{'Автопарк'}</a>
                    </li>
                    <li>
                        <a href="" onClick={ActiveMenu}>{'Отзывы'}</a>
                    </li>
                    <li>
                        <a href="#Contacts" onClick={ActiveMenu}>{'Контакты'}</a>
                    </li>
                </ul>
            </div>
            <div className={BurgerStyleMenu.column} onClick={ActiveMenu}>
                <span></span>
                <span></span>
            </div>
            <div className={BurgerStyleMenu.column}>
                <div className={BurgerStyleMenu.column_box}>
                    <img src={auto} alt="" />
                    <a href="#">{'+7(910)903-45-45'}
                    </a>
                </div>
                <div className={BurgerStyleMenu.column_box}>
                    <img src={cargoTruck} alt="" />
                    <a href="#">{'+7(910)645-45-45'}
                    </a>
                </div>
            </div>
        </div>
    )
}