import React from 'react'
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Scrollbar, Autoplay } from 'swiper';
import 'swiper/swiper.scss';
import 'swiper/components/scrollbar/scrollbar.scss';
import SwiperStyle from '../scss/SwiperStyle.module.scss'

SwiperCore.use([Scrollbar, Autoplay]);

export const SwiperJsTablet = () => {
    return (
        <Swiper
            spaceBetween={30}
            slidesPerView={2}
            centeredSlides={true}
            // onSlideChange={() => console.log('slide change')}
            // onSwiper={(swiper) => console.log(swiper)}
            className={SwiperStyle.swiperContainer}
            loop={true}
            autoplay={{delay: 2000}}
            disableOnInteraction={true}
        >
            <SwiperSlide className={SwiperStyle.swiperSlide}></SwiperSlide>
            <SwiperSlide className={SwiperStyle.swiperSlide}></SwiperSlide>
            <SwiperSlide className={SwiperStyle.swiperSlide}></SwiperSlide>
            <SwiperSlide className={SwiperStyle.swiperSlide}></SwiperSlide>
            <SwiperSlide className={SwiperStyle.swiperSlide}></SwiperSlide>
        </Swiper>
    )
}